* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

body {
  color: #222;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.modal {
  width: 335px;
  height: 535px;
  min-height: 250px;
  z-index: 2;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  gap: .4rem;
  padding: 1.3rem;
  display: flex;
  position: absolute;
  top: 20%;
}

.modal .flex {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.modal input {
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: .7rem 1rem;
  font-size: .9em;
}

.modal p {
  color: #777;
  margin: .4rem 0 .2rem;
  font-size: .9rem;
}

button {
  cursor: pointer;
  border: none;
  font-weight: 600;
}

.btn {
  color: #fff;
  text-align: center;
  background-color: #6d8434;
  border-radius: 30px;
  padding: .8rem 1.4rem;
  font-size: 1em;
  font-weight: 700;
  display: inline-block;
}

.btn-open {
  position: absolute;
  bottom: 150px;
}

.btn-close {
  background: #eee;
  border-radius: 50%;
  padding: .5rem .7rem;
  transform: translate(10px, -20px);
}

.overlay {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 1;
  background: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.hidden {
  display: none;
}

.btnplusimg {
  gap: 40px;
  display: flex;
}

